@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@400;600&display=swap);
body,#root,.wrapper{margin:0;min-height:100vh}.wrapper{display:flex;justify-content:center;align-items:center}.wrapper_dark{background-color:#1E1F27}*{font-size:14px;line-height:1.2;font-family:'Exo 2', sans-serif}

.field{position:relative;color:#fff;font-size:14px;display:flex;flex-direction:column;padding-bottom:18px}.field__input{color:#fff;padding:12px 10px 11px;border:none;border-radius:4px}.field__input_dark{background-color:#1E1F27}.field__label{color:#CFD2E3;padding-bottom:10px;line-height:1.2;cursor:pointer}.field__info{position:absolute;bottom:0;font-size:12px}.field__info_error{color:#FF7549}.field__info_message{color:#777B8E}

.form{display:flex;flex-direction:column;background-color:#2A2C35;padding:40px 40px 0;border-radius:12px;width:380px;box-sizing:border-box}.form__title{color:#CFD2E3;font-size:36px;font-weight:400;margin:0 0 40px;text-align:center}.form__link{align-self:center}.form .field{margin-bottom:20px}.form .field:last-of-type{margin-bottom:60px}.form:last-child{padding-bottom:69px}.form.form_second:last-child{padding-bottom:31px}.form.form_second .form__button{margin-bottom:21px}

.button{border-radius:4px;padding-top:11px;padding-bottom:12px;border:none;cursor:pointer}.button_purple{color:#fff;background-color:#855AFF}.button:disabled{opacity:.5;cursor:default}.button-link{background:transparent;color:#855AFF;padding:0}

